// Common styles of the project

*,
*::before,
*::after {
    box-sizing: border-box;
}

html, body {
    height: 100%;
}

html {
    font-family: $font-1;

    @include below($tablet) {
        font-size: 22px !important;
    }

    @include below(900px) {
        font-size: 20px !important;
    }

    @include below(800px) {
        font-size: 18px !important;
    }

    @include below(700px) {
        font-size: 16px !important;
    }

    @include below($mobile) {
        font-size: 16 / 420 * 100 + vw !important;
    }
}

.hide-scroll {
    overflow: hidden;

    @include below($tablet) {
        position: fixed;
        width: 100%;

        body {
            overflow: hidden;
            position: fixed;
            width: 100%;
        }
    }

}

.overlay {
    filter: blur(5px);
}
